import { instagramData } from "./instagramData.js";

const instagrammListEl = document.querySelector(".instagramm__list");

function instRender(data) {
  return data
    .map(({ name, alt }) => {
      return `<li class="instagramm__item">
              <img
                src="./image/instagram-profile/${name}.jpg"
                alt="${alt}"
                class="instagramm__img"
                height="300"
                width="400"
              />
            </li>`;
    })
    .join("");
}

instagrammListEl.innerHTML = instRender(instagramData);

